import { mapGetters } from "vuex";
import { createHelpers } from "vuex-map-fields";
import { required, url, minLength, email } from "vuelidate/lib/validators";

const { mapFields } = createHelpers({
  getterType: "pins/getField",
  mutationType: "pins/updateField",
});

export default {
  name: "PinProfile",
  components: {},
  props: {
    item: {
      type: Object,
      default: null,
    },
    fetch: {
      type: Boolean,
      default: false,
    },
    bgColor: {
      type: String,
      default: "#254C26",
    },
  },
  data() {
    return {
      waiting: false,
      valid: true,
      step: 0,
      lastStep: 0,
      active: false,
      background: this.bgColor,
      files: null,
      file: null,
    };
  },
  validations: {
    categoryId: {
      required,
    },
    title: {
      required,
    },
    email: {
      // required,
      email,
    },
    phone: {
      minLength: minLength(11),
    },
    street: {
      required,
    },
    number: {
      required,
    },
    neighborhood: {
      required,
    },
    city: {},
    cep: {
      required,
      minLength: minLength(8),
    },
    description: {
      required,
    },
    link: {
      url,
    },
    facebook: {
      url,
    },
    instagram: {
      url,
    },
    twitter: {
      url,
    },
    whatsapp: {},
    files: {
      required,
    },
  },
  computed: {
    ...mapGetters({
      categories: "categories/loadCategories",
    }),
    ...mapFields({
      category: "categorySelected",
      categoryId: "pinForm.categoryId",
      title: "pinForm.title",
      email: "pinForm.email",
      phone: "pinForm.phone",
      street: "pinForm.street",
      number: "pinForm.number",
      neighborhood: "pinForm.neighborhood",
      city: "pinForm.city",
      cep: "pinForm.zipcode",
      description: "pinForm.description",
      images: "pinForm.imageIds",
      link: "pinForm.link",
      facebook: "pinForm.facebook",
      instagram: "pinForm.instagram",
      twitter: "pinForm.twitter",
      whatsapp: "pinForm.whatsapp",
    }),
    categoryErrorMessage() {
      if (!this.$v.categoryId.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    titleErrorMessage() {
      if (!this.$v.title.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    emailErrorMessage() {
      if (!this.$v.email.email) {
        return "Entre com um email vlido";
      }
      return "";
    },
    phoneErrorMessage() {
      if (!this.$v.phone.minLength) {
        return "Entre com um telefone vlido";
      }
      return "";
    },
    streetErrorMessage() {
      if (!this.$v.street.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    numberErrorMessage() {
      if (!this.$v.number.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    neighborhoodErrorMessage() {
      if (!this.$v.neighborhood.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    cityErrorMessage() {
      if (!this.$v.city.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    cepErrorMessage() {
      if (!this.$v.cep.required) {
        return "Esse campo  requerido";
      } else if (!this.$v.cep.minLength) {
        return "Entre com um cep vlido";
      }
      return "";
    },
    descriptionErrorMessage() {
      if (!this.$v.description.required) {
        return "Esse campo  requerido";
      }
      return "";
    },
    linkErrorMessage() {
      if (!this.$v.link.url) {
        return "Entre com uma url vlida";
      }
      return "";
    },
    facebookErrorMessage() {
      if (!this.$v.facebook.url) {
        return "Entre com uma url vlida";
      }
      return "";
    },
    instagramErrorMessage() {
      if (!this.$v.instagram.url) {
        return "Entre com uma url vlida";
      }
      return "";
    },
    twitterErrorMessage() {
      if (!this.$v.twitter.url) {
        return "Entre com uma url vlida";
      }
      return "";
    },
    whatsappErrorMessage() {
      return "";
    },
    filesErrorMessage() {
      if (!this.$v.files.required) {
        return " necessrio uma imagem.";
      }
      return "";
    },
    // mascara para telefone
    phoneMask() {
      if (this.phone === null || this.phone === undefined) {
        console.log("phone undefined");
        return false;
      }
      let str = "";
      const p = this.phone;
      console.log("p", this.phone);
      const ddd = p.slice(0, 2);
      const prefix = p.slice(2, 7);
      const sufix = p.slice(7, 11);
      str = str.concat(ddd).concat("").concat(prefix).concat(" ").concat(sufix);
      console.log(str);
      return str;
    },
  },
  created() {},
  methods: {
    confirmCreate() {
      this.$v.$touch();
      if (!this.$v.$anyError) {
        if (this.waiting) {
          this.$q.notify({
            message: "Por favor, aguarde.",
          });
          return;
        }
        this.waiting = true;
        this.$store
          .dispatch("images/uploadArray", { files: this.files })
          .then((fileIds) => {
            console.log("image: ", fileIds);
            this.images = fileIds;
            this.$store.dispatch("pins/postPin", { $router: this.$router })
              .then(()=>{
                this.categoryId = '';
                this.title = '';
                this.email = '';
                this.phone = '';
                this.street = '';
                this.number = '';
                this.neighborhood = '';
                this.city = '';
                this.cep = '';
                this.description = '';
                this.images = '';
                this.link = '';
                this.facebook = '';
                this.instagram = '';
                this.twitter = '';
                this.whatsapp = '';
              });
            this.waiting = false;
          })
          .catch((error) => {
            this.waiting = false;
            console.log(error);
          });
      } else {
        this.$q.notify({
          message: "Por favor, preencha os campos corretamente.",
        });
      }
    },
    updateFiles(files) {
      if (Array.isArray(files) === false || files.length === 0) {
        this.files = null;
      } else if (Array.isArray(this.files) === true) {
        const diff = this.files.filter((file) => files.indexOf(file) === -1);

        if (diff.length === 1 && this.files.length > 1) {
          this.files = files.slice();
        } else if (this.files.length < 3) {
          this.files = diff.concat(files);
        }
      } else {
        this.files = files.slice();
      }
    },
    showForm() {
      if (this.step === 0) {
        this.lastStep = 0;
        this.active = true;
        const a = this;
        setTimeout(() => {
          a.step = 1;
        }, 800);
        console.log("iniciando primeira edio", this.lastStep, this.step);
      }
    },
    reEdit() {
      this.lastStep = 2;
      const a = this;
      setTimeout(() => {
        a.step = 1;
      }, 1000);
      console.log("reeditando", this.lastStep, this.step);
    },
    cancelEdit() {
      console.log("cancelando", this.lastStep, this.step);
      if (this.lastStep === 0) {
        this.active = false;
        const a = this;
        setTimeout(() => {
          a.step = 0;
        }, 1000);
        this.lastStep = 1;
        console.log("cancelando primeira edio", this.lastStep);
      }
      if (this.lastStep === 2) {
        const a = this;
        setTimeout(() => {
          a.step = 2;
        }, 1000);
        console.log("cancelando reedio");
        this.lastStep = 2;
      }
    },
    expand() {
      this.state = !this.state;
    },
  },
};
