export default {
  name: "TopicProfile",
  props: {},
  data() {
    return {
      barStyle: {
        right: "0",
        borderRadius: "4px",
        backgroundColor: "#fff",
        width: "4px",
        opacity: .8,
      },
    };
  },
  computed: {},
  methods: {
    mask(text){
      const limit = 20;
      if (text.length>limit) return text.substring(0, limit)+'...';
      return text;
    },
    async emitThisTopic(topic) {
      this.$router.push({
        name: "TopicPage",
        params: { topicId: topic.id },
      });
    },
  },
};
